.ConsultingCategoryItem {
  &-container {
    padding: 20px 16px 0;
    background: var(--aquamarin-60);
    border-radius: 24px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.2s ease-in-out;
    &:hover {
      background: var(--aquamarin-80);
    }
    @media screen and (max-width: 860px) {
      margin-bottom: 30px;
      flex-direction: row;
    }
    @media screen and (max-width: 480px) {
      flex-direction: column;
    }
    &.assistant {
      background: var(--gradient-petrol-light);
      &:hover {
        background: var(--gradient-petrol-light-hover);
      }
    }
    &.webinar {
      background: var(--gradient-purple-blue-light);
      &:hover {
        background: var(--gradient-purple-blue-light-hover);
      }
    }
  }
  &-up {
    max-width: 85%;
    color: var(--nightblue-100);
    @media screen and (max-width: 860px) {
      max-width: 50%;
    }
    @media screen and (max-width: 480px) {
      max-width: 100%;
    }
  }
  &-title {
    font-size: 27px;
    font-weight: 500;
    line-height: 31px;
    letter-spacing: -0.01em;
  }
  &-desc {
    margin: 8px 0 0;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: -0.02em;
  }
  &-image {
    margin-top: 16px;
    display: flex;
    justify-content: center;
    @media screen and (max-width: 860px) {
      margin: 0 60px 0 0;
      min-width: 30%;
      justify-content: flex-start;
    }
    @media screen and (max-width: 680px) {
      margin: 0;
    }
    @media screen and (max-width: 480px) {
      margin-top: 16px;
      align-self: center;
    }
    & > img {
      max-width: 100%;
      height: 220px;
      object-fit: cover;
      @media screen and (max-width: 680px) {
        object-fit: contain;
      }
    }
  }
}
