.CategoryItem {
  &-container {
    padding: 16px;
    background: var(--aquamarin-60);
    border-radius: 24px;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    &:hover {
      background: var(--aquamarin-80);
    }
    &.assistant {
      background: var(--petrol-10);
      &:hover {
        background: var(--petrol-20);
      }
    }
  }
  &-title {
    color: var(--nightblue-100);
    font-size: 27px;
    font-weight: 500;
    line-height: 31px;
  }
  &-footer {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    color: var(--petrol-80);
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    @media screen and (max-width: 860px) {
      margin-top: 24px;
    }
    & .text {
      padding: 7px 10px;
      background: #fff;
      border-radius: 30px;
    }
    & .icon {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 30px;
      height: 30px;
      font-size: 16px;
      font-weight: bold;
      background: #fff;
      border-radius: 12px;
      cursor: pointer;
    }
  }
}
