/**
* Set Schedule
*/
.SetSchedule {
  &-lb {
    margin-bottom: 8px;
    color: var(--nightblue-100);
    font-size: 13px;
  }
  &-chooseDate {
    margin-bottom: 15px;
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid var(--petrol-10);
    @media screen and (max-width: 860px) {
      margin-bottom: 30px;
      padding-bottom: 30px;
    }
    & > div {
      width: calc(50% - 15px);
      @media screen and (max-width: 860px) {
        width: 100%;
      }
    }
  }
  &-datePicker {
    @media screen and (max-width: 860px) {
      margin-top: 15px;
    }
    & .FormGroupDatePicker {
      &-area {
        margin: 0;
      }
      &-container {
        & > input {
          height: 51px;
        }
        & > .hb-datepicker {
          width: 51px;
          height: 51px;
        }
      }
    }
  }
  &-timesOptions {
    display: flex;
    align-items: center;
    & > div {
      margin-bottom: 0;
      flex-grow: 1;
    }
    & > span {
      margin: 0 16px;
      color: var(--nightblue-100);
      font-size: 13px;
    }
  }
  &-options {
    & > button {
      height: 51px;
      font-size: 17px;
      border-radius: 16px;
    }
  }
  &-actions {
    margin-top: 30px;
    & > button {
      width: 100%;
      height: 45px;
      font-size: 15px;
      justify-content: center;
      border-radius: 12px;
    }
  }
}
