.AppointmentItem {
  &-container {
    padding: 16px;
    background: #fff;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    &:hover {
      &:not(.detail) {
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
      }
    }
    & .MenuOptions-area {
      top: 100%;
      left: auto;
      right: -19px;
      &:after {
        top: -11px;
        left: auto;
        right: 24px;
        transform: rotateZ(90deg);
      }
    }
  }
  &-schedule {
    color: var(--nightblue-100);
    font-size: 13px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.01em;
    display: flex;
    justify-content: space-between;
    position: relative;
    @media screen and (max-width: 360px) {
      font-size: 12px;
    }
  }
  &-btnMenu {
    width: 20px;
    height: 30px;
    color: var(--petrol-80);
    font-size: 24px;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
  }
  &-topic {
    margin-bottom: 0;
    color: var(--nightblue-100);
    font-size: 19px;
    font-weight: 500;
    line-height: 25px;
    letter-spacing: -0.01em;
    word-break: break-word;
  }
  &-userInfo {
    margin-top: 10px;
    display: flex;
  }
  &-ava {
    margin-right: 8px;
    width: 40px;
    height: 40px;
  }
  &-userName {
    margin: 0;
    max-width: calc(100% - 48px);
    color: var(--nightblue-80);
    font-size: 13px;
    line-height: 21px;
    letter-spacing: -0.01em;
    & > p {
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  &-status {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: var(--nightblue-100);
    font-size: 13px;
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.01em;
    & > .icon {
      margin-right: 8px;
    }
    &.hasBorder {
      margin-top: 10px;
      padding-top: 10px;
      border-top: 1px solid var(--petrol-10);
    }
    &.requested {
      display: inline-flex;
      padding: 8px 10px;
      background: var(--aquamarin-20);
      border-radius: 8px;
      & > i {
        margin-right: 8px;
        color: var(--petrol-100);
        font-size: 17px;
      }
    }
    &.accepted {
      min-height: 65px;
      &.hasBorder {
        border-top: none;
        min-height: auto;
      }
      & > button {
        width: 100%;
        justify-content: center;
        border-radius: 12px;
        height: 45px;
      }
    }
    &.finished {
      & > button {
        width: 100%;
        justify-content: center;
        border-radius: 12px;
        height: 45px;
      }
      & > .vote {
        padding: 8px 10px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: var(--aquamarin-20);
        border-radius: 8px;
        & > .rate {
          display: inline-flex;
          align-items: center;
          color: var(--nightblue-100);
          font-size: 13px;
          font-weight: 500;
          line-height: 22px;
          letter-spacing: 0.01em;
          & > .icon {
            margin-right: 8px;
          }
        }
        & > button {
          height: 36px;
        }
        @media screen and (max-width: 860px) {
          height: 44px;
          & > button {
            height: 28px;
          }
        }
      }
    }
    &.cancelled {
      & .reason {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 8px 10px;
        background: var(--alert-20);
        border-radius: 8px;
        & > span {
          &:last-child {
            margin-left: 8px;
            max-width: calc(100% - 22px);
          }
        }
      }
    }
  }
}

.AppointmentTagInfo {
  &-container {
    margin: 0 8px 8px 0;
    padding: 4px 12px;
    display: inline-flex;
    align-items: center;
    background: var(--petrol-5);
    border-radius: 8px;
    &:last-child {
      margin-right: 0;
    }
    & > i {
      margin-right: 8px;
      color: var(--petrol-80);
      font-size: 15px;
      font-weight: bold;
    }
  }
}
