.MenuOptions {
  &-area {
    display: none;
    position: absolute;
    min-width: 135px;
    top: -20px;
    left: calc(100% + 6px);
    z-index: 1;
    &.open {
      display: inline-flex;
    }
    &:after {
      content: '';
      position: absolute;
      top: 24px;
      left: -6px;
      width: 0;
      height: 0;
      border-top: 8px solid transparent;
      border-bottom: 8px solid transparent;
      border-right: 6px solid var(--petrol-20);
    }
    @media screen and (max-width: 1350px) {
      top: 100%;
      left: auto;
      right: -19px;
      &:after {
        top: -11px;
        left: auto;
        right: 24px;
        transform: rotateZ(90deg);
      }
    }
  }
  &-list {
    margin: 0;
    list-style-type: none;
    display: inline-flex;
    flex-direction: column;
    background: #fff;
    border: 1px solid #fff;
    border-radius: 16px;
    box-shadow: 0 1px 8px rgba(153, 153, 153, 0.3);
    overflow: hidden;
    & > li {
      padding: 16px;
      color: var(--petrol-80);
      font-size: 13px;
      font-weight: 500;
      border-bottom: 1px solid var(--petrol-5);
      cursor: pointer;
      &:last-child {
        border-bottom: none;
      }
      &:hover {
        background: #f1f2f3;
      }
      & > i {
        font-size: 24px;
        vertical-align: middle;
      }
    }
  }
}
